/* mobile toggle  */
@media only screen and (max-width: 600px) {
    #ktl-dashboard-sidebar {
      position: fixed;
      z-index: 2;
      display: none;
      bottom: 0;
      top: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 100%;
      overflow-y:auto ;
    }
  
    /* #ktl-dashboard-main{
      position: absolute;
    } */

    #ktl-dashboard-mobile-toggle{
        display: block;
      }

    #ktl-dashboard-desktop-toggle{
        display: none;
    }
  }

  @media only screen and (min-width: 1200px) {
    #ktl-dashboard-mobile-toggle{
        display: none;
      }

  }
  
  @font-face {
    font-family: ktltitle;
    src: url(KaushanScript-Regular.otf);
  }
  
  